<div class="download-results-main"><floating-modal v-if="showDownloadResults" title="DownloadResults" @closeModal="$emit(&quot;hideDownloadResults&quot;)"><div class="download-body"><div class="title">Select your desired file type and then click download.</div><div class="options"> <label for="file-type-select">File type:</label><select id="file-type-select" v-model="fileType"><option value="pb.gz">pb.gz</option><option value="csv" disabled>csv (coming soon)</option><option value="pbtxt" disabled>pbtxt (coming soon)</option></select></div><div class="download"><button @click="downloadResults">Download {{fileType}} file</button></div></div></floating-modal></div>