<div class="reaction-container"><div class="row" :class="isSelected ? &quot;selected&quot; : &quot;&quot;"><div class="select" v-if="isSelectable"><input type="checkbox" :id="&quot;select_&quot;+reaction.reaction_id" :value="reaction.reaction_id" :checked="isSelected" @change="$emit(&quot;clickedSelect&quot;, $event)"><label :for="&quot;select_&quot;+reaction.reaction_id">Select reaction</label></div><div class="reaction-table" v-html="reactionTable" v-if="reactionTable"></div><LoadingSpinner v-else></LoadingSpinner><div class="info"><div class="col full"><router-link :to="{ name: &quot;reaction-view&quot;, params: {reactionId: reaction.reaction_id}}"> <button>View Full Details</button></router-link></div><div class="col"><div class="yield">Yield: {{getYield(reaction.data.outcomesList[0].productsList[0].measurementsList)}}</div><div class="conversion">Conversion: {{getConversion(reaction.data)}}</div><div class="conditions">Conditions: {{conditionsAndDuration(reaction.data).join("; ") || "Not Listed"}}</div><div class="smile" v-if="reaction.data.outcomesList[0].productsList[0].identifiersList.length"><CopyButton :textToCopy="reaction.data.outcomesList[0].productsList[0].identifiersList[0].value"></CopyButton><div class="value">Product {{productIdentifier(reaction.data.outcomesList[0].productsList[0].identifiersList[0])}}</div></div></div><div class="col"><div class="creator">Uploaded by {{reaction.data.provenance.recordCreated.person.name}}, {{reaction.data.provenance.recordCreated.person.organization}}</div><div class="date">Uploaded on {{new Date(reaction.data.provenance.recordCreated.time.value).toLocaleDateString()}}</div><div class="doi">DOI: {{reaction.data.provenance.doi}}</div><div class="publication"> <a :href="reaction.data.provenance.publicationUrl" target="_blank">Publication URL</a></div><div class="dataset">Dataset: {{reaction.dataset_id}}</div></div></div></div></div>